"use client";

import { useRouter } from "next/navigation";
import { AppShell } from "@/components/layout/app-shell";
import Link from "next/link";
import { useState } from "react";

export default function NewDashboardPage() {
  const router = useRouter();
  const [creating, setCreating] = useState(false);

  async function handleCreate() {
    setCreating(true);
    try {
      const res = await fetch("/api/dashboards", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ name: "New Dashboard", theme: "auto" }),
      });
      const data = await res.json();
      if (data.id) router.push(`/dashboards/${data.id}`);
    } finally {
      setCreating(false);
    }
  }

  return (
    <AppShell activeTab="/dashboards">
      <div className="space-y-4">
        <h2 className="text-xl font-semibold">New dashboard</h2>
        <p className="text-sm text-gray-500 dark:text-gray-400">
          <Link href="/onboarding" className="underline">Run onboarding</Link> to set up your first dashboard, or create an empty one and add widgets.
        </p>
        <button
          type="button"
          onClick={handleCreate}
          disabled={creating}
          className="rounded-full bg-accent-yellow dark:bg-accent-green px-4 py-2 text-sm font-medium text-gray-900 disabled:opacity-50"
        >
          {creating ? "Creatingâ€¦" : "Create empty dashboard"}
        </button>
      </div>
    </AppShell>
  );
}
